import React from "react";
import Box from "grommet/components/Box";
import Animate from "grommet/components/Animate";

export default class Index extends React.Component {
  constructor() {
    super();
    this.state = {
      color: undefined
    }
  }
  componentDidMount() {
    let colors = this.props.setColorOfContent();
    this.setState({
      color: colors.home
    });
  }

  render() {
    let style = {
      backgroundColor: this.state.color + "1a"
    }
    return (
      <Animate
        enter={{ animation: "slide-right", duration: 300, delay: 0 }}
        keep={true}
      >
        <Box className="content-box index" style={style}>
          <h1 className="content-title">About</h1>
          <p>Hi, my name is Tihomir Selak and I'm an web developer.</p>
          <p>Been working in video production for many years, than made a transition web development 500ms ease. If I would have to pick three words from all this, they would be <a href="https://www.adobe.com/products/aftereffects.html" target="_blank">After Effect</a>, <a href="https://reactjs.org/" target="_blank">React</a> and <a href="https://greensock.com/" target="_blank">Gsap</a>.</p>
          <p>Outside of the Realm in Real World I also enjoy cycling and comics.</p>
        </Box>
      </Animate>
    );
  }
}



